import md from './doc.md';

export default {
    components: { md },
    data() {
        return {
            showChangeBtn: false,
        }
    },
    methods: {
        handleZoom(type) {
            let map = this.$refs.leaflet.getMap();
            let zoom = map.getZoom();
            if (type === 1) {
                zoom += 1;
            } else {
                zoom -= 1;
            }
            map.setZoom(zoom);
        },
        handleChange(type) {
            let baseLayer = this.$refs.baseLayer;
            baseLayer.changeTileServe(type);
        },
        handleSelfServe() {
            let serviceConfigData = {
                skymapImage1: {
                    url: "http://t0.tianditu.gov.cn/img_c/wmts?tk=3133b000f831d898dc3a8f9bcff6ddf4",
                    opt: {
                        layer: "img",
                        tileMatrixSet: "c",
                        format: "tiles",
                        style: "default",
                        type: "baseLayer"
                    },
                },
                skymapImage2: {
                    url: "http://t0.tianditu.gov.cn/cia_c/wmts?tk=3133b000f831d898dc3a8f9bcff6ddf4",
                    opt: {
                        layer: "cia",
                        tileMatrixSet: "c",
                        format: "tiles",
                        style: "default",
                        type: "baseLayer"
                    },
                }
            }
            let baseLayer = this.$refs.baseLayer;
            baseLayer.setcustomServe('wmts', serviceConfigData);
        }
    }
}