<template>
    <div class="base-template">
        <div style="padding-bottom:20px;">
            <el-tooltip effect="light">
                <div slot="content">
                    <pre>
let map = this.$refs.leaflet.getMap();
let zoom = map.getZoom();
map.setZoom(zoom+1);
                    </pre>
                </div>
                <el-button size="small" @click="handleZoom(1)">放大</el-button>
            </el-tooltip>
            <el-tooltip effect="light">
                <div slot="content">
                    <pre>
let map = this.$refs.leaflet.getMap();
let zoom = map.getZoom();
map.setZoom(zoom-1);
                    </pre>
                </div>
                <el-button size="small" @click="handleZoom(0)">缩小</el-button>
            </el-tooltip>
            <el-tooltip effect="light">
                <div slot="content">
                    <pre>
let baseLayer = this.$refs.baseLayer;
baseLayer.changeTileServe('skymapVec');
                    </pre>
                </div>
                <el-button size="small" @click="handleChange('skymapVec')">切换为矢量</el-button>
            </el-tooltip>
            <el-tooltip effect="light">
                <div slot="content">
                    <pre>
let baseLayer = this.$refs.baseLayer;
baseLayer.changeTileServe('skymapTopography');
                    </pre>
                </div>
                <el-button size="small" @click="handleChange('skymapTopography')">切换为地形</el-button>
            </el-tooltip>
            <el-tooltip effect="light">
                <div slot="content">
                    <pre>
let baseLayer = this.$refs.baseLayer;
baseLayer.changeTileServe('skymapImage');
                    </pre>
                </div>
                <el-button size="small" @click="handleChange('skymapImage')">切换为影像</el-button>
            </el-tooltip>
            <el-tooltip effect="light">
                <div slot="content">
                    <pre>
html：为wl-base-layer添加属性showChangeBtn="show"
js:this.show = !this.show;
                    </pre>
                </div>
                <el-button size="small" @click="showChangeBtn=!showChangeBtn">显隐切换按钮</el-button>
            </el-tooltip>
            <el-tooltip effect="light">
                <div slot="content">
                    <pre>
html 为wl-base-layer添加属性:serviceType="'wmts'" :serviceConfig="serviceConfigData"
js中：
let serviceConfigData = {
    skymapImage1: {
        url: "http://t0.tianditu.gov.cn/img_c/wmts?tk=3133b000f831d898dc3a8f9bcff6ddf4",
        opt: {
            layer: "img",
            tileMatrixSet: "c",
            format: "tiles",
            style: "default",
            type: "baseLayer"
        },
    },
    skymapImage2: {
        url: "http://t0.tianditu.gov.cn/cia_c/wmts?tk=3133b000f831d898dc3a8f9bcff6ddf4",
        opt: {
            layer: "cia",
            tileMatrixSet: "c",
            format: "tiles",
            style: "default",
            type: "baseLayer"
        },
    }
}
                    </pre>
                </div>
                <el-button size="small" @click="handleSelfServe">设置自定义服务为天地图的影像</el-button>
            </el-tooltip>
        </div>
        <wang-leaflet ref="leaflet" style="height:50vh;">
            <wl-base-layer ref="baseLayer" :showChangeBtn="showChangeBtn"
                :defaultTileServe="['skymapVec', 'skymapTopography', 'skymapImage']"></wl-base-layer>
        </wang-leaflet>
        <md class="markdown-body"></md>
    </div>
</template>

<script>
    import _script from "./index.js";
    export default _script;
</script>

<style lang="less" scoped>
    // @import url("./style.less");
</style>